                img: 'tiketting_rsu_tangerang.png',
                text: 'Tiketting RSU Tangerang'
            },
            {
                img: 'skp_borobudur.png',
                text: 'SKP Borobudur'
            },
            {
                img: 'skp_kpp_pratama.png',
                text: 'SKP KPP Pratama'
            },
            {
                img: 'tiketting_ahu.png',
                text: 'Tiketting AHU'
            },
            {
                img: 'more_slide.png',
                text: 'Want See More, Please Contact Me :)'
            },
            // android
        ])
        return {
            umur,
            exp,
            portofolios,
            modules: [Navigation, Pagination, Scrollbar, A11y, Autoplay, EffectCoverflow],
        };
 
    },
    components:{
        VLazyImage,
